import { mapGetters } from 'vuex';
export default {
  name: 'Footer',
  computed: {
    ...mapGetters(['userConfig']),
    footerLink() {
      return this.userConfig?.footerLink || 'https://blog.o0w0b.top';
    }
  },
  data() {
    return {
      copyrightText: '',
      // 版权文本
      runningTime: '' // 运行时间文本
    };
  },
  methods: {
    // 更新运行时间
    updateTime() {
      const start = new Date("2025/02/01 19:26:11");
      const now = new Date();
      const diff = now.getTime() - start.getTime();
      const dayMs = 24 * 60 * 60 * 1000;
      const days = Math.floor(diff / dayMs);
      const remMs = diff % dayMs;
      const hours = Math.floor(remMs / (60 * 60 * 1000));
      const remMsHr = remMs % (60 * 60 * 1000);
      const mins = Math.floor(remMsHr / (60 * 1000));
      const remMsMin = remMsHr % (60 * 1000);
      const secs = Math.floor(remMsMin / 1000);
      this.runningTime = `本站已运行：${days}天${hours}小时${mins}分${secs}秒`;
    }
  },
  mounted() {
    // 设置版权信息
    const year = new Date().getFullYear();
    this.copyrightText = `© 2025 - ${year} By 开心就好`;

    // 每秒更新一次运行时间
    setInterval(this.updateTime, 1000);
  }
};